.conversation {
  width: 80px;
  background-color: #0073b0;
  position: relative;
  display: block;
  color: white;
  font-size: 14px;
  text-align: center;
  padding: 5px 0px;
  left: 60%; }
  .conversation:after {
    position: absolute;
    content: "";
    /* bottom: -16px; */
    left: 12%;
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 15px 15px 0 0;
    border-color: #0073b0 transparent transparent transparent;
    top: 22px; }

.App {
  text-align: center;
  padding: 0px 20px; }
  .App:before {
    content: "";
    position: absolute;
    top: 0px;
    left: 0px;
    right: 0px;
    height: 515px;
    background-image: url(https://images.unsplash.com/photo-1461749280684-dccba630e2f6?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=1650&q=80);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
    z-index: -1; }
  .App:after {
    z-index: -1;
    content: "";
    position: absolute;
    z-index: 1;
    top: 0px;
    left: 0px;
    right: 0px;
    height: 515px;
    background-color: rgba(44, 51, 64, 0.8);
    z-index: -1; }

